import "./styles.css";
import React, { useState } from "react";

export default function App() {
  const [fontHeight, setFontHeight] = useState("1em");

  const [fontWeight, setFontWeight] = useState("normal");

  console.log({ fontHeight });
  console.log({ fontWeight });

  const changeToSix = () => {
    setFontHeight("6rem");
    setFontWeight("bold");
  };

  const changeToFour = () => {
    setFontHeight("4rem");
    setFontWeight("bold");
  };

  const changeToTwoFont = () => {
    setFontHeight("2rem");
    setFontWeight("800");
  };

  const changeToBack = () => {
    setFontHeight("1rem");
    setFontWeight("normal");
  };

  return (
    <div className="App">
      <button onClick={changeToSix}> h1 </button>

      <button onClick={changeToFour}> h2 </button>

      <button onClick={changeToTwoFont}> h3 </button>

      <button onClick={changeToBack}> back </button>

      <div style={{ fontSize: fontHeight, fontWeight: fontWeight }}>
        I am a complan girl! {fontHeight} {fontWeight}
      </div>
    </div>
  );
}
